.section {
  min-height: 100vh;
  width: 100;
  background: #4361ee;
  display: -webkit-flex;
  display: flex;
  padding: 1.5rem;
  padding-top: 3rem;
  -webkit-flex-direction: column-reverse;
          flex-direction: column-reverse;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  justify-items: center;
  -webkit-align-content: space-evenly;
          align-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
}
.section-alt {
  background: #ffffff;
}

.section .home-text {
  font-weight: 500;
  text-align: left;
}

.section .section-title {
  text-align: center;
  margin: 0;
}

.home-btn {
  cursor: pointer;
  padding: 0.5rem;
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  font-weight: 500;
  font-size: 24px;
  text-decoration: none;
  color: black;
  background: #4361ee;
  border-radius: 4px;
  transition: 0.3s ease;
  margin: 10px;
  border: 1px darkblue;
}

.home-btn:hover {
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
  box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.4);
  background: #4361ee;
}

.home-btn:active {
  -webkit-transform: translateY(-1px);
          transform: translateY(-1px);
  background: #4361ee;
  opacity: 100%;
}

.home-btn-alt {
  color: #000001;
  background: #F5F5F5;

}
.home-btn-alt:hover{
  background: #E8E8E8;
}

.home-btn-alt:active{
  background: #E8E8E8;
}


@media screen and (min-width: 1200px) {
  .section .section-title {
    text-align: left;
    margin: 0;
  }

  .section {
    text-align: justify;
    padding: 4rem;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  }

  .section-text {
